<template>
  <div id="XuDebounce" ref="XuDebounce" @click="btnMouseup(s)">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "XuDebounce",
  props: {
    s: {
      //
      type: Number,
      default: 3000,
    },
    prompt: {
      //
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      switch: true,
    };
  },
  methods: {
    btnMouseup(s) {
      //
      if (this.switch) {
        this.switch = false;
        let that = this;
        setTimeout(function() {
          that.switch = true;
          if (that.prompt) {
            console.log(s / 1000 + "s");
          }
        }, s);
        this.$emit("debonce", "");
      } else {
        if (this.prompt) {
          console.error(s / 1000 + "s");
        }
      }
    },
  },
};
</script>
