class Execute {
  constructor() {
    this.value = undefined
    this.indexNNN = []
  }

  /**
   * @param {} value
   * @returns boolean
   *  :false,true
   */
  sameValueOnlyOnce(value) {
    let switchV
    if (value != this.value) {
      this.value = value
      switchV = true
    } else {
      switchV = false
    }
    return switchV
  }

  /**
   * @param {} index
   * @returns 
   *  
   */
  differenceComparison(index) {
    let num
    if (this.indexNNN[0] == undefined) {
      this.indexNNN[0] = index
      num = undefined
    } else {
      if (this.indexNNN[1] == undefined) {
        this.indexNNN[1] = index
        num = this.indexNNN[1] - this.indexNNN[0]
      } else {
        this.indexNNN[0] = JSON.parse(JSON.stringify(this.indexNNN[1]))
        this.indexNNN[1] = index
        num = this.indexNNN[1] - this.indexNNN[0]
      }
    }
    return num
  }
}

export { Execute }
